<template>
  <div>
    <div v-if="title" class="title anim-title is-3 has-text-centered">
      <div>{{ title || '\u00A0' }}</div>
    </div>
    <div class="anim-area is-flex" :style="{ visibility: __prerendering ? 'hidden' : '' }">
      <div class="code-sizer-parent box is-marginless is-size-7-mobile">
        <code ref="codeSizer" style="white-space: pre-wrap; font-size: 1.25em">
        </code>
      </div>

      <slot/>

      <iframe
        :style="{ width: codeSize.width + 'px', height: codeSize.height + 'px' }"
        ref="codeFrame"
        v-html="currentFrameHtml"
        title="Code samples"
        :class="[{ 'is-active': showCode }, 'code-frame box is-marginless is-size-7-mobile']"/>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import $ from '../vendor/cash'
import 'blast-text'
import Prism from 'prismjs'
import initialFrameHtml from '../assets/codeFrame.html'

export default Vue.extend({
  data () {
    return {
      title: '',
      showCode: false,
      currentFrameHtml: initialFrameHtml,
      codeSize: {
        width: 0,
        height: 0
      },
      active: true,
      resolveActive: null
    }
  },

  methods: {
    async start (sections) {
      for (const [title, func] of Object.entries(sections)) {
        await this.timeout(0)
        await whenScrolledInView(this.$el)

        this.title = title

        await func()
        await this.timeout(200)
      }
    },

    async updateCodeFrameSize (text) {
      const { codeFrame, codeSizer } = this.$refs

      if (typeof text === 'string') {
        codeSizer.textContent = (text)
      }

      this.codeSize = { width: 0, height: 0 }
      await this.$nextTick()

      const { width } = this.$el.getBoundingClientRect()
      const { height } = codeSizer.getBoundingClientRect()

      this.codeSize = { width, height }
      codeFrame.contentDocument.documentElement.style.fontSize =
        getComputedStyle(codeFrame).fontSize
      await this.$nextTick()
    },

    async codeDemo ({
      code = '',
      lang,
      append,
      delimiter = 'word',
      speed = delimiter === 'character' ? 20 : 40,
      noUpdateSize = !!append
    }) {
      const { codeFrame } = this.$refs

      if (!noUpdateSize) {
        await this.timeout(0)
        await this.updateCodeFrameSize(code)
      }

      await this.timeout(0)

      if (!this.showCode) {
        await this.initFrame()
      }

      const frameBody = codeFrame.contentDocument.body
      const transparent = 'rgba(0, 0, 0, 0)'
      const $pre = $('pre', frameBody)
      const $code = $('<code>')
      const prevDelimiter = $pre.find('[data-blast]').attr('data-blast') || delimiter

      // erase previous code
      if (!append) {
        for (const segment of $code.blast({ delimiter: prevDelimiter, aria: false }).toArray().reverse()) {
          segment.style.color = transparent
          segment.classList.add('cursor')
          await this.timeout(speed / 2)

          segment.classList.remove('cursor')
        }

        $pre.empty()
      }

      if (Prism.languages[lang]) {
        $code.html(Prism.highlight(code, Prism.languages[lang]))
      } else { $code.text(code) }

      $code.appendTo($pre)

      this.currentFrameHtml = codeFrame.contentDocument.documentElement.innerHTML

      const segments = $code
        .blast({ delimiter, aria: false })
        .css('color', transparent)
        .toArray()

      for (const segment of segments) {
        segment.classList.add('cursor')
        await this.timeout(speed)

        segment.style.color = ''
        segment.classList.remove('cursor')
      }
    },
    async initFrame () {
      const { codeFrame } = this.$refs
      const html = this.currentFrameHtml

      this.currentFrameHtml = ''
      await this.$nextTick()
      this.currentFrameHtml = html

      this.showCode = true
      $(codeFrame).siblings().removeClass('is-active')

      codeFrame.contentDocument.documentElement.innerHTML = this.currentFrameHtml

      // clear iframe selection
      $(document).on('selectionchange touchstart', () => {
        if (this.active === true) {
          codeFrame.contentWindow.getSelection().empty()
        }
      })

      await this.$nextTick()
    },
    async setInactive () {
      if (this.active === true) {
        this.active = new Promise(resolve => {
          this.resolveActive = () => {
            resolve(true)
            this.active = true
          }
        })
        await this.$nextTick()
      }
    },
    timeout (duration) {
      return new Promise(resolve => {
        setTimeout(() => resolve(this.active), duration)
      })
    }
  },
  mounted () {
    $(window).on('resize', () => this.updateCodeFrameSize())
  },
  destroyed () { this.setInactive() },
  deactivated () { this.setInactive() },
  activated () {
    if (this.active !== true) {
      const { codeFrame } = this.$refs

      this.resolveActive()
      codeFrame.src = ''

      codeFrame.onload = () => {
        codeFrame.contentWindow.document.innerHTML = this.currentFrameHtml
        if (this.showCode) {
          this.initFrame()
        }
      }
    }
  }
})

function isInView (element) {
  if (document.hidden) { return true }

  const { top, bottom, height } = element.getBoundingClientRect()

  const margin = Math.min(height / 2, window.innerHeight / 3, 400)

  if (top + margin < Math.max(window.innerHeight, window.scrollY) &&
    bottom > margin) {
    return true
  }

  return false
}

async function whenScrolledInView (element) {
  return new Promise(resolve => {
    if (isInView(element)) {
      resolve(null)
      return
    }

    window.addEventListener('scroll', onScroll)
    window.addEventListener('resize', onScroll)

    function onScroll () {
      if (isInView(element)) {
        window.removeEventListener('scroll', onScroll)
        window.removeEventListener('resize', onScroll)
        resolve(null)
      }
    }
  })
}

</script>
