import Vue from 'vue'
import App from './App.vue'
import components from './components'
Vue.config.productionTip = false

for (const [name, component] of Object.entries(components)) {
  Vue.component(name, component)
}

const packageInfo = {
  version: document.getElementById('app').dataset.packageVersion
}

Vue.mixin({
  methods: {
    $getAsset: (path) => require('@/assets/' + path)
  },
  beforeCreate () {
    Vue.util.defineReactive(this, '$packageInfo', packageInfo)
  }
})

Vue.prototype.__prerendering = !!window.__PRERENDER_INJECTED
Vue.prototype.__prerendered = !!document.querySelector('#app[data-server-rendered]')

window.__adWrapper = document.getElementById('ad-wrapper')

;(async () => {
  const router = (await import(/* webpackChunkName: "router" */ './router')).default

  new Vue({
    router,
    render: h => h(App)
  }).$mount('#app')

  fetch('https://data.jsdelivr.com/v1/package/resolve/npm/interactjs@latest')
    .then(r => r.json())
    .then(data => {
      packageInfo.version = data.version
    })
    .catch(() => {})
})()
