<template>
  <div>
    <div
      v-for="(link, index) of links"
      :key="index"
      :class="['navbar-item', 'is-paddingless', { 'has-dropdown is-hoverable': isDropdown(link), 'is-mobile': mobile }]">
      <template v-if="isDropdown(link)">
        <div class="navbar-link">
          <img :src="link[2]" class="is-hidden-mobile white-icon" :alt="link[3]">
          {{ link[1] }}
        </div>

        <div class=navbar-dropdown aria-label=dropdown>
          <IntExtLink v-for="[text, url] of Object.entries(link[0])" :key="text" :to="url" class="navbar-item">
          {{ text }}
          </IntExtLink>
        </div>
      </template>
      <template v-else>
        <IntExtLink :to="link[0]" class="navbar-link is-arrowless">
          <img :src="link[2]" class="is-hidden-mobile white-icon" :alt="link[3]">
          {{ link[1] }}
        </IntExtLink>
      </template>

    </div>
  </div>
</template>

<script>
/* eslint-disable standard/array-bracket-even-spacing */
import Vue from 'vue'

export default Vue.extend({
  props: {
    mobile: Boolean
  },
  data () {
    return {
      links: [
        [
          {
            Intro: '/docs/',
            Draggable: '/docs/draggable',
            Resizable: '/docs/resizable',
            Gesturable: '/docs/gesturable',
            Dropzone: '/docs/dropzone',
            'Action Options': '/docs/action-options',
            Events: '/docs/events',
            Modifiers: '/docs/modifiers',
            Snapping: '/docs/snapping',
            Restriction: '/docs/restriction',
            Inertia: '/docs/inertia',
            Reflow: '/docs/reflow',
            FAQ: '/docs/faq',
            API: '/docs/api/Interactable.html'
          }, 'Docs', require('@/assets/doc.svg'), 'Docs'
        ],
        ['https://github.com/taye/interact.js', '\u00A0Source', require('@/assets/code.svg'), 'Code' ],
        ['https://gitter.im/taye/interact.js', 'Chat', require('@/assets/chat.svg'), 'Gitter'],
        ['https://twitter.com/interactjs', '@interactjs', require('@/assets/bird.svg'), 'Twitter' ]
      ]
    }
  },
  computed: {
    routePath () { return this.$route.path }
  },
  methods: {
    isDropdown (link) {
      return typeof link[0] === 'object'
    }
  },
  watch: {
    routePath () {
      Array.from(this.$el.querySelectorAll('.has-dropdown')).forEach(el => {
        el.style.pointerEvents = 'none'
        el.getBoundingClientRect()

        setTimeout(() => {
          el.style.pointerEvents = ''
        }, 200)
      })
    }
  }
})

</script>
